import React, { useEffect, useRef } from 'react';
import SubmitButton from './common/SubmitButton';
import { useTranslation } from 'react-i18next';

interface HeaderProps {
  heightList: Array<{ id: string; height: number; background: string }>;
  scrollToEvent: () => void;
  sessionModal: boolean;
}

function Header({ heightList, scrollToEvent, sessionModal }: HeaderProps) {
  const { t, i18n } = useTranslation();
  const handleLanguage = (lang: string) => {
    i18n.changeLanguage(lang);
  };

  return (
    <>
      <div className="logo">
        <a href="/">
          <img src="img/logo.svg" alt="logo" />
        </a>
      </div>
      <div className="uiWrapper">
        <a
          href="https://forms.office.com/Pages/ResponsePage.aspx?id=lo8J4xs2xkep9Kt7r8r_6fY16GdzuMRJohJyF0ZGpUlUQ0taR0hHN1FHRDlKUzg5QUVRQUo1RzZZSS4u"
          target="_blank"
        >
          <SubmitButton title="register now" heightList={heightList} sessionModal={sessionModal} />
        </a>
        <button className="goEvent" onClick={scrollToEvent}>
          event
        </button>
        {/* 이벤트 섹션으로 클릭시 이동 */}
        {i18n.language === 'en' && (
          <button className="langToggle" onClick={() => handleLanguage('ko')}>
            {t('korean')}
          </button>
        )}
        {i18n.language === 'ko' && (
          <button className="langToggle" onClick={() => handleLanguage('en')}>
            {t('english')}
          </button>
        )}
      </div>
    </>
  );
}

export default Header;
