import { ErrorModal, ErrorModalActionTypes, OPEN_ERROR_MODAL, CLOSE_ERROR_MODAL } from './types';

const initialState: ErrorModal = {
  errorModal: false,
  errorMessage: '',
};

const errorReducer = (state = initialState, action: ErrorModalActionTypes) => {
  switch (action.type) {
    case OPEN_ERROR_MODAL:
      return {
        ...state,
        errorModal: true,
        errorMessage: action.errorMessage,
      };
    case CLOSE_ERROR_MODAL:
      return {
        ...state,
        errorModal: false,
        errorMessage: '',
      };
    default:
      return state;
  }
};

export default errorReducer;
