import React, { useEffect, useRef, useState } from 'react';
import Header from './Header';
import useError from 'hooks/useError';
import ErrorModal from './common/ErrorModal';
import '../css/main.scss';
import Questions from './Questions';
import { scroller, Events } from 'react-scroll';
import useOnScreen from 'hooks/useOnScreen';
import { useTranslation } from 'react-i18next';
import { evnetFAQList, registerFAQList, etcFAQList } from 'lib/value/faq';
import { componentsInfo } from 'lib/value/componentsInfo';
import Speaker from './Speaker';
import { speakerList } from 'lib/value/speakerList';
import { scheduleList } from 'lib/value/scheduleList';
import Schedule from './Schedule';
import CommonModal from 'components/common/CommonModal';

function MainComponent() {
  const { t, i18n } = useTranslation();
  const { errorModal } = useError();
  const divRef = useRef<HTMLDivElement>(null);
  const headerRef = useRef<HTMLHeadElement>(null);
  const loadingRef = useRef<HTMLDivElement>(null);
  const scrollRef = useRef<HTMLButtonElement>(null);
  const onScreen = useOnScreen(divRef);

  const section1Ref = useRef<HTMLDivElement>(null);
  const contentPageRef = useRef<HTMLDivElement>(null);
  const section2Ref = useRef<HTMLDivElement>(null);
  const section3Ref = useRef<HTMLDivElement>(null);
  const section4Ref = useRef<HTMLDivElement>(null);
  const section5Ref = useRef<HTMLDivElement>(null);
  const section6Ref = useRef<HTMLDivElement>(null);
  const section7Ref = useRef<HTMLDivElement>(null);
  const section8Ref = useRef<HTMLDivElement>(null);

  const [sessionModal, setSessionModal] = useState(false);
  const [session, setSession] = useState('');

  const [modalType, setModalType] = useState('session');

  const [heightList, setHeightList] = useState<
    Array<{ id: string; height: number; background: string }>
  >(componentsInfo);

  useEffect(() => {
    if (onScreen) {
      window.addEventListener('mousemove', onCursorChange);
      divRef.current && divRef.current.addEventListener('click', movePage);
    } else {
      window.removeEventListener('mousemove', onCursorChange);
      divRef.current && divRef.current.removeEventListener('click', movePage);
    }
  }, [onScreen]);

  useEffect(() => {
    if (!loadingRef.current) {
      return;
    }

    loadingRef.current.classList.add('finishLoading');
    loadingRef.current.addEventListener('animationend', () => {
      if (!loadingRef.current) {
        return;
      }

      loadingRef.current.style.display = 'none';
    });
  }, []);
  useEffect(() => {
    setTimeout(onResize, 500);
    window.addEventListener('resize', onResize);
    window.addEventListener('scroll', onScrollEvent);
  }, []);

  useEffect(() => {
    if (window.matchMedia('(min-width: 780px)').matches) {
      divRef.current && divRef.current.addEventListener('click', movePage);
    }

    window.addEventListener('resize', () => {
      if (window.matchMedia('(min-width: 780px)').matches) {
        divRef.current && divRef.current.addEventListener('click', movePage);
      }
    });
  }, [divRef.current]);

  const onResize = () => {
    setHeightList(
      heightList.map((data, idx) => {
        if (idx === 0) {
          data.height = section1Ref.current
            ? section1Ref.current.getBoundingClientRect().height
            : 0;
        } else if (idx === 1) {
          data.height = contentPageRef.current
            ? contentPageRef.current.getBoundingClientRect().height
            : 0;
        } else if (idx === 2) {
          data.height = section2Ref.current
            ? section2Ref.current.getBoundingClientRect().height
            : 0;
        } else if (idx === 3) {
          data.height = section3Ref.current
            ? section3Ref.current.getBoundingClientRect().height
            : 0;
        } else if (idx === 4) {
          data.height = section4Ref.current
            ? section4Ref.current.getBoundingClientRect().height
            : 0;
        } else if (idx === 5) {
          data.height = section5Ref.current
            ? section5Ref.current.getBoundingClientRect().height
            : 0;
        } else if (idx === 6) {
          data.height = section6Ref.current
            ? section6Ref.current.getBoundingClientRect().height
            : 0;
        } else if (idx === 7) {
          data.height = section7Ref.current
            ? section7Ref.current.getBoundingClientRect().height
            : 0;
        } else if (idx === 8) {
          data.height = section8Ref.current
            ? section8Ref.current.getBoundingClientRect().height
            : 0;
        }

        return data;
      })
    );
  };

  const onScrollEvent = () => {
    const height = window.innerHeight;
    const yOffset = window.pageYOffset;
    if (!headerRef.current) {
      return;
    }

    if (yOffset > 0) {
      if (scrollRef.current && scrollRef.current.style.display !== 'none') {
        scrollRef.current.style.display = 'none';
      }
    } else {
      if (scrollRef.current) {
        scrollRef.current.style.display = 'block';
      }
    }

    if (yOffset > height) {
      if (headerRef.current.style.display !== 'flex') {
        headerRef.current.style.display = 'flex';
      }
    } else {
      headerRef.current.style.display = 'none';
    }
  };

  const onCursorChange = (e: MouseEvent) => {
    if (onScreen) {
      const windowWidth = window.innerWidth;
      const windowHalf = windowWidth / 2;

      if (!divRef.current) {
        return;
      }

      if (e.clientX > windowHalf) {
        divRef.current.style.cursor = 'url("./img/right_cursor.cur"), auto';
      } else if (e.clientX < windowHalf) {
        divRef.current.style.cursor = 'url("./img/left_cursor.cur"), auto';
      }
    }
  };

  const movePage = (e: MouseEvent) => {
    if (onScreen) {
      const windowWidth = window.innerWidth;
      const windowHalf = windowWidth / 2;

      if (!divRef.current) {
        return;
      }

      if (e.clientX > windowHalf) {
        scroll('left', 300);
      } else if (e.clientX < windowHalf) {
        scroll('right', 300);
      }
    }
  };

  function scroll(axis: string, duration: number) {
    let currentTime = 0;
    const increment = 8;
    const animateScroll = function () {
      currentTime += increment;

      if (!divRef.current) {
        return;
      }

      if (axis === 'left') {
        divRef.current.scrollLeft =
          divRef.current.scrollLeft > 0 ? divRef.current.scrollLeft + 20 : 20;
      } else if (axis === 'right') {
        divRef.current.scrollLeft =
          divRef.current.scrollLeft > 0 ? divRef.current.scrollLeft - 20 : 0;
      }

      if (currentTime < duration) {
        setTimeout(animateScroll, increment);
      }
    };

    animateScroll();
  }

  const scrollTo = () => {
    const scrollFirst = new Promise((resolve, reject) => {
      Events.scrollEvent.register('end', () => {
        resolve();
        Events.scrollEvent.remove('end');
      });

      scroller.scrollTo('content_page', {
        duration: 1200,
        delay: 0,
        smooth: 'ease',
      });
    });

    scrollFirst.then(() => {
      if (!headerRef.current) {
        return;
      }

      headerRef.current.style.display = 'flex';
    });
  };

  const scrollToEvent = () => {
    scroller.scrollTo('evnet_page', {
      duration: 0,
      delay: 0,
      smooth: 'ease',
    });
  };

  const openSessionModal = (e: React.MouseEvent<HTMLElement>) => {
    const sessionName = e.currentTarget.getAttribute('data-session');

    if (sessionName === 'privacy') {
      setModalType('privacy');
    } else {
      setModalType('session');
    }
    setSession(sessionName || '');
    setSessionModal(true);
  };

  const closeSessionModal = () => {
    setSessionModal(false);
    setSession('');
  };

  return (
    <>
      <div className="loadingBlk" ref={loadingRef}></div>
      <header ref={headerRef}>
        <Header heightList={heightList} scrollToEvent={scrollToEvent} sessionModal={sessionModal} />
      </header>
      <div className="fixedBG">
        <video autoPlay muted loop playsInline src="/small_all_DeMain.mp4"></video>
      </div>
      <div className="section fullHeight titleWrapper" ref={section1Ref}>
        <div className="title">
          <img src="/img/title.svg" alt="title" />
        </div>
        <h5>Coupang Developer Conference 2020.12.11</h5>
        <button className="scrollDown" ref={scrollRef}>
          <a onClick={() => scrollTo()}>
            <img src="/img/hero-scroll-down.svg" alt="scroll down" />
          </a>
        </button>
      </div>
      <div id="content_page">
        <div className="section fullHeight decsiptionWrapper" ref={contentPageRef}>
          <div className="description">
            <h5>{t('description1')}</h5>
            <h5>{t('description2')}</h5>
            <h5>{t('description3')}</h5>
          </div>
        </div>
        <div className="section blkBg speakerWrapper" ref={section2Ref}>
          <div className="speakers">
            <h4>speakers</h4>
            <div className="speakerList" ref={divRef}>
              {speakerList.map((speaker) => (
                <Speaker speaker={speaker} key={speaker.name} />
              ))}
              <span className="likePadding"></span>
            </div>
          </div>
        </div>
        <div className="section fullHeight" ref={section3Ref}></div>
        <div className="section blkBg sessionWrapper" ref={section4Ref}>
          <div className="contents">
            <h4>schedule</h4>
            {scheduleList.map((schedule, idx) => (
              <Schedule
                schedule={schedule}
                key={schedule.time + idx}
                onOpenModal={openSessionModal}
              />
            ))}
          </div>
        </div>
        <div className="section fullHeight" ref={section5Ref}></div>
        <div className="section blkBg faqWrapper" ref={section6Ref}>
          <div className="contents">
            <h4>faq</h4>
            {/* 등록 정보 */}
            <p className="faqSubTitle">
              {t('faqSubTitle1')}
              {/* Registration Information */}
            </p>
            <div className="singleContentWrapper show noPadding"></div>
            {registerFAQList.map((data) => (
              <Questions
                question={i18n.language === 'en' ? data.enQuestion : data.koQuestion}
                answer={i18n.language === 'en' ? data.enAnswer : data.koAnswer}
                key={data.koQuestion}
              />
            ))}
            {/* 이벤트 정보 */}
            <p className="faqSubTitle">
              {t('faqSubTitle2')}
              {/* About the Event */}
            </p>
            <div className="singleContentWrapper show noPadding"></div>
            {evnetFAQList.map((data) => (
              <Questions
                question={i18n.language === 'en' ? data.enQuestion : data.koQuestion}
                answer={i18n.language === 'en' ? data.enAnswer : data.koAnswer}
                key={data.koQuestion}
              />
            ))}
            {/* 그밖에.. */}
            <p className="faqSubTitle">
              {t('faqSubTitle3')}
              {/* Others */}
            </p>
            <div className="singleContentWrapper show noPadding"></div>
            {etcFAQList.map((data) => (
              <Questions
                question={i18n.language === 'en' ? data.enQuestion : data.koQuestion}
                answer={i18n.language === 'en' ? data.enAnswer : data.koAnswer}
                key={data.koQuestion}
              />
            ))}
          </div>
        </div>
        <div id="evnet_page" className="section fullHeight byeWrapper" ref={section7Ref}>
          {/* 이벤트 섹션임 */}
          <h4>event</h4>
          <h6 className="bolder firstEvent">{t('eventSubTitle1')}</h6>
          <h6 className="bolder eventDetail">{t('eventDetail1_1')}</h6>
          <h6 className="howTo">
            <span className="bolder">{t('eventDetail1_2')}</span>
            <span> 
              <a
              href="https://forms.office.com/Pages/ResponsePage.aspx?id=lo8J4xs2xkep9Kt7r8r_6UTjp0OsYH5Mk-71eiWJ3i9UM1dQUFlXVE1OUFlGNkI4UjNHTURVMlZXVS4u"
              target="_blank">
              {t('eventDetail1_3')}
              </a>
              {t('eventDetail1_3_1')}
              {t('eventDetail1_4')}
            </span>
          </h6>
          <h6 className="eventPeriod">
            <span className="bolder">{t('eventDetail1_5')}</span>
            <span>{t('eventDetail1_6')}</span>
          </h6>
          <h6 className="bolder secondEvent">{t('eventSubTitle2')}</h6>
          <h6 className="bolder">{t('eventDetail2_1')}</h6>
          <h6 className="lastNotice">{t('eventDetail3')}</h6>
          {/* <h5>{t('byeMSG')}</h5>
          <h6>
            coupang career (
            <a href="https://www.linkedin.com/company/coupang/" target="_blank">
              linkedin
            </a>
            ,
            <a href="https://www.facebook.com/coupang.recruit/" target="_blank">
              facebook
            </a>
            )
          </h6> */}
        </div>
      </div>
      <footer ref={section8Ref}>
        <div className="companyLogo">
          <img src="img/coupang_logo.svg" alt="coupang logo" />
        </div>
        <div className="footerUi">
          <p className="privacyPolicy" onClick={openSessionModal} data-session="privacy">
            {t('privacyPolicy')}
          </p>
          <p>
            <span>©</span>Coupang Corp.
          </p>
        </div>
      </footer>
      {errorModal && <ErrorModal />}
      {sessionModal && (
        <CommonModal
          type={modalType}
          targetId={session}
          open={sessionModal}
          onClose={closeSessionModal}
        />
      )}
    </>
  );
}

export default MainComponent;
