import React, { useEffect, useState } from 'react';
import { DialogContent } from '@material-ui/core';
import { useTranslation } from 'react-i18next';
import { sessionENList, sessionKOList } from 'lib/value/sessionList';

interface SessionInfoProps {
  targetId: string;
}
function SessionInfo({ targetId }: SessionInfoProps) {
  const { i18n } = useTranslation();
  const [session, setSession] = useState<{
    id: string;
    title: string;
    content: Array<string>;
    presenters: Array<{ name: string; title: string; dept: string; content: string }>;
  }>();

  useEffect(() => {
    const searchList = i18n.language === 'en' ? sessionENList : sessionKOList;

    const sessionList = searchList.filter((target) => target.id === targetId);

    if (sessionList[0]) {
      setSession(sessionList[0]);
    }
  }, [i18n.language]);

  return (
    <DialogContent>
      <div className="scheduleTitle">{session && session.title}</div>
      {session &&
        session.content.map((data) => (
          <div className="scheduleContent" key={data}>
            {data}
          </div>
        ))}
      <br />
      {session &&
        session.presenters.map((data) => (
          <div className="speakerWrapper" key={data.name}>
            <div className="speakerName">{data.name} :</div>
            <div className="speakerTitle">
              {data.title}
            </div>
            <div className="speakerDescription">{data.content}</div>
          </div>
        ))}
    </DialogContent>
  );
}

export default SessionInfo;
