import React from 'react';

interface SpeakerProps {
  speaker: { class: string; name: string; title: string };
}

function Speaker({ speaker }: SpeakerProps) {
  return (
    <div className="speakerCard">
      <div className={`speakerProfileImg ${speaker.class}`}></div>
      <div className="speakerName">
        <p>{speaker.name}</p>
      </div>
      <div className="speakerTitle">
        <p>{speaker.title}</p>
      </div>
    </div>
  );
}

export default Speaker;
