import React, { useRef, useEffect, useState } from 'react';
import useOnScreen from 'hooks/useOnScreen';
require('intersection-observer');

interface QuestionProps {
  question: string;
  answer: string;
}

function Questions({ question, answer }: QuestionProps) {
  const divRef = useRef<HTMLDivElement>(null);
  const buttonRef = useRef<HTMLButtonElement>(null);
  const contentRef = useRef<HTMLDivElement>(null);
  const onScreen = useOnScreen(divRef);
  const [isShow, setIsShow] = useState(false);

  useEffect(() => {
    if (onScreen) {
      divRef.current && divRef.current.classList.add('show');
    }
  }, [onScreen]);

  const showContent = () => {
    setIsShow(!isShow);
    if (!isShow) {
      buttonRef.current && buttonRef.current.classList.add('toMinus');
      if (contentRef.current) contentRef.current.style.maxHeight = '160px';
    } else {
      buttonRef.current && buttonRef.current.classList.remove('toMinus');
      if (contentRef.current) contentRef.current.style.maxHeight = '0px';
    }
  };

  return (
    <div className="singleContentWrapper" ref={divRef}>
      <p className="singleContent">
        <span className="singleFaqTitle">{question}</span>
        <button className="showDetail" onClick={showContent} ref={buttonRef}></button>
        <div className="faqDetail" ref={contentRef}>
          <span></span> 
          {/* works like padding */}
          {answer}
        </div>
      </p>
    </div>
  );
}

export default Questions;
