import { useSelector, useDispatch } from 'react-redux';
import { RootState } from 'store';
import { useCallback } from 'react';
import { closeErrorModal, openErrorModal } from 'store/error/actions';

export default function useError() {
  const { errorModal, errorMessage } = useSelector((state: RootState) => state.error);
  const dispatch = useDispatch();

  const onCloseModal = useCallback(() => dispatch(closeErrorModal()), [dispatch]);
  const onOpenModal = useCallback(
    ({ errorMessage }: { errorMessage: string }) => dispatch(openErrorModal({ errorMessage })),
    [dispatch]
  );

  return { errorModal, errorMessage, onCloseModal, onOpenModal };
}
