import React from 'react';
import { Dialog, DialogContent, DialogActions } from '@material-ui/core';
import useError from 'hooks/useError';
import SubmitButton from './SubmitButton';

function ErrorModal() {
  const { errorModal, errorMessage, onCloseModal } = useError();
  return (
    <Dialog onClose={onCloseModal} open={errorModal} aria-labelledby="error-dialog">
      <DialogContent>{errorMessage}</DialogContent>
      <DialogActions>
        <SubmitButton title="close" onClick={onCloseModal} />
      </DialogActions>
    </Dialog>
  );
}

export default ErrorModal;
