export interface ErrorModal {
  errorModal: boolean;
  errorMessage: string;
}

export const OPEN_ERROR_MODAL = 'OPEN_ERROR_MODAL';
export const CLOSE_ERROR_MODAL = 'CLOSE_ERROR_MODAL';

export interface OpenErrorModalAction {
  type: typeof OPEN_ERROR_MODAL;
  errorMessage: string;
}

export interface CloseErrorModalAction {
  type: typeof CLOSE_ERROR_MODAL;
}

export type ErrorModalActionTypes = OpenErrorModalAction | CloseErrorModalAction;
