import React, { Suspense } from 'react';
import './App.css';
import { Route } from 'react-router-dom';
import MainComponent from 'components/Main';
import 'lang/i18n';

function App() {
  return (
    <Suspense fallback={null}>
      <Route exact path="/" component={MainComponent} />
    </Suspense>
  );
}

export default App;
