import React, { useEffect, useRef } from 'react';
import { useTranslation } from 'react-i18next';
import useOnScreen from 'hooks/useOnScreen';

interface ScheduleProps {
  schedule: {
    sessionId: string;
    singleSession: boolean;
    time: string;
    hoverable: boolean;
    koTitle: string;
    enTitle: string;
    sessionList?: Array<{
      time: string;
      koTitle: string;
      enTitle: string;
      brInsert: boolean;
    }>;
  };
  onOpenModal: (e: React.MouseEvent<HTMLElement>) => void;
}
function Schedule({ schedule, onOpenModal }: ScheduleProps) {
  const { i18n } = useTranslation();
  const divRef = useRef<HTMLDivElement>(null);
  const onScreen = useOnScreen(divRef);

  useEffect(() => {
    if (onScreen) {
      divRef.current && divRef.current.classList.add('show');
    }
  }, [onScreen]);

  return (
    <div className="singleContentWrapper" ref={divRef}>
      {schedule.singleSession || !schedule.sessionList ? (
        <h6
          className={`singleContent ${schedule.hoverable && 'hoverable'}`}
          onClick={(schedule.hoverable && onOpenModal) || undefined}
          data-session={schedule.sessionId}
        >
          <span className="time">{schedule.time}</span>
          <span className="sessionTitle">
            {i18n.language === 'en' ? schedule.enTitle : schedule.koTitle}
          </span>
        </h6>
      ) : (
        schedule.sessionList.map((session, idx) => (
          <h6 className={`singleContent hoverable talk_${idx + 1}`} key={session.koTitle}>
            <span className="time">{session.time}</span>
            <span className="sessionTitle">
              {i18n.language === 'en' ? session.enTitle : session.koTitle}
              {session.brInsert && (
                <>
                  <br />
                  &nbsp;
                </>
              )}
            </span>
          </h6>
        ))
      )}
    </div>
  );
}

export default Schedule;
