import React, { useEffect, useRef } from 'react';
import { useTranslation } from 'react-i18next';

interface SubmitButtonProps {
  title: string;
  onClick?: () => void;
  disabled?: boolean;
  heightList?: Array<{ id: string; height: number; background: string }>;
  sessionModal?: boolean;
}

function SubmitButton({ title, onClick, disabled, heightList, sessionModal }: SubmitButtonProps) {
  const buttonRef = useRef<HTMLButtonElement>(null);
  const { t } = useTranslation();

  useEffect(() => {
    window.addEventListener('scroll', changeButtonStyle);
    return () => {
      window.removeEventListener('scroll', changeButtonStyle);
    };
  }, []);

  useEffect(() => {
    changeButtonStyle();
  }, [sessionModal]);

  const changeButtonStyle = () => {
    const pageYOffset = window.pageYOffset;
    let currentHeight = 0;
    if (!buttonRef.current || !heightList) {
      return;
    }
    const buttonMargin = 24;

    for (let i = 0; i < heightList.length - 1; i++) {
      const browserBottomPos = pageYOffset + window.innerHeight;

      if (currentHeight < browserBottomPos) {
        currentHeight += heightList[i].height;
      }

      if (
        currentHeight < browserBottomPos - buttonMargin &&
        browserBottomPos - buttonMargin < currentHeight + heightList[i + 1].height
      ) {
        console.log(sessionModal);
        if (heightList[i + 1].background === 'black' || sessionModal) {
          buttonRef.current && buttonRef.current.classList.add('lineType');
          break;
        } else {
          buttonRef.current && buttonRef.current.classList.remove('lineType');
          break;
        }
      }
    }
  };

  return (
    <button
      className={`${heightList && 'modalUiButton'}`}
      onClick={onClick || undefined}
      disabled={disabled || false}
      ref={buttonRef}
    >
      {t(`${title}`)}
    </button>
  );
}

export default SubmitButton;
